


















import Vue from 'vue';

export default Vue.extend({
  name: 'ARadio',
  inheritAttrs: false,
  model: {
    prop: 'checked',
    event: 'change',
  },
  props: {
    /* eslint-disable vue/require-default-prop */
    checked: null as any,
    value: null as any,
    /* eslint-enable vue/require-default-prop */
    label: { type: String, default: '' },
    disabled: Boolean,
  },
  computed: {
    localChecked: {
      get(): any {
        return this.checked;
      },
      set(checked: any) {
        this.$emit('change', checked);
      },
    },
  },
});
